@use "./scss/" as *;
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Roboto:wght@300;400;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-size: 100%;
  background-color: $grayBlue;
  font-family: "Roboto", sans-serif;
}
.container {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}
a {
  text-decoration: none;
  color: $white;
}
